import './App.css';
import React, { useState, useContext, useEffect, useRef, PureComponent } from 'react';
import { BarChart, Bar, Cell, XAxis, YAxis, CartesianGrid, Tooltip, Legend, ResponsiveContainer } from 'recharts';


// 프로젝트에 필요한 데이터 객체를 구성한다
// 요청 데이터기간(1일: DAILY, 1개월: MONTH, 3개월: 3MONTH)
const terms = ['DAILY', 'MONTH', '3MONTH'];

const names = [
  { stationName: '종로구' },
  { stationName: '마포구' },
  { stationName: '강남구' }
]

const endPoint = 'https://apis.data.go.kr/B552584/ArpltnInforInqireSvc/getMsrstnAcctoRltmMesureDnsty'
const serviceKey = 'scenMfZwGHORNW7VXYf7LSYR2AGgLY2LqqRWLadnUI7PJpU4HjgSneEu6HUtuLbcWyGZegzDXF3%2FfcMp7aHOuw%3D%3D';
const TType = 'json';
const numOfRows = 100;
const pageNo = 1;
const ver = 1.0;


// 서버에 데이터를 요청하는 함수
function fetchData(name, term) {

  // 자바스크립트에 내장된 fetch() 메서드를 사용하여 서버에 요청한다
  const promise = fetch(`${endPoint}?serviceKey=${serviceKey}&returnType=${TType}&numOfRows=${numOfRows}&pageNo=${pageNo}&stationName=${name.stationName}&dataTerm=${term}&ver=${ver}`)
    .then(res => {
      // 서버의 응답코드(status)가 200(성공)이 아닌 경우 catch 블록에 응답 객체를 던진다
      if (!res.ok) { 
        throw res;
      }
      // 서버의 응답코드가 200인 경우 응답객체(프로미스 객체)를 리턴한다
      return res.json();
    })

  return promise;
}

// 메인 컴포넌트 
export default function App() {

  const [term, setTerm] = useState(terms[0]);
  const [name, setName] = useState(names[0]);

  return (
    <>
      {/* <!-- NAVBAR --> */}
      <nav className="navB">
        <ul className="flex">
          <li className="w14">
            <a href="#" className="navBa">HOME</a>
          </li>
          <li className="w14">
            <a href="#information" className="navBa">측정정보 조회</a>
          </li>
          <li className="w14">
            <a href="#menu" className="navBa">지역별 미세먼지 차트</a>
          </li>
          <li className="w14">
            <a href="#where" className="navBa">지도</a>
          </li>
        </ul>
      </nav>

      {/* <!-- MAIN IMAGE --> */}
      <div className="mainImg">
        <div className="mainImgS">
          <h1 className="mainImgF">
            {name.stationName} 미세먼지 오염 정보
          </h1>
        </div>
      </div>

      {/* 메인 */}
      <main className='NoMargin'>
        <h1>측정정보 조회</h1>
        <section>
          <h3>서울</h3>
          {names.map(name => (
            <button
              key={name.id}
              onClick={() => setName(name)}
            >
              {name.stationName}
            </button>
          ))}
        
        </section>
        <div id="select-term">
        <select onChange={(e) => setTerm(e.target.value)}>
            {terms.map(term => (
              <option key={term} value={term}>{term}</option>
            ))}
          </select>
        </div>

        {/* 대시보드에 city와 day변수를 전달한다 */}
        <Dashboard name={name} term={term} />
      </main>
      
    </>
  )
}

// 대시보드 
function Dashboard({ name, term }) {

  const [data, setData] = useState(null);
  const [isLoaded, setIsLoaded] = useState(false);
  const [error, setError] = useState(null);

  // console.log(data);
  // console.log(JSON.parse(JSON.stringify(data)))
  // console.log(`DATA : ${JSON.stringify(data, null, "\t")}`);

  useEffect(() => {

    // 서버에 요청하기 전 사용자에게 대기 상태를 먼저 보여주어야 한다
    setIsLoaded(false); 
    setError(null);

      // fetchData함수에 name와 term 변수를 전달한다
      fetchData(name, term)
        .then(data => {
          setData(data);
        })
        .catch(error => {
          setError(error);
        })
        .finally(() => setIsLoaded(true)); // 성공 실패와 관계없이 서버가 응답하면 대기상태를 해제한다

  }, [name, term]) // name 또는 term 변수가 업데이트되면 서버에 다시 데이터를 요청한다

  if (error) {
    return <p>failed to fetch</p>
  }

  if (!isLoaded) {
    return <p>fetching data...</p>
  }

  // console.log(data);

  const items = data.response.body.items;

  // console.log(items)

  const Dustdata = data; 

  return (
    <>
      <h1>{term}주기 {name.stationName} 미세먼지 오염 정보</h1>
      
      {data.response.body.totalCount > 0 ? (
        <>
          {/* DATA를 합성된 컴포넌트에 전달한다 */}
          <p>자료가 있습니다</p>

<p>총 {data.response.body.totalCount}개의 결과가 나왔습니다</p>
<table className='dusttable'>
    <thead>
      <tr>
        <th>미세먼지(PM10) 농도</th>
        <th>통합대기환경수치</th>
        <th>측정 시간대</th>
      </tr>
    </thead>
    <tbody>
    {items.map((item, index) => (
      <tr>
        <td>
          <li style={{textAlign: "center"}}>{item["pm10Value"]} pm</li>
        </td>
        <td>
          <li style={{textAlign: "center"}}>{item["khaiValue"]}</li>
        </td>
        <td>
          <li style={{textAlign: "center"}}>{item["dataTime"]}</li>
        </td>
        
      </tr>
    ))}
    </tbody>
    
  </table>
  <Recharts className="NoMargin cen" Dustdata={items} />
        </>
      ) : (
        // 데이터가 없으면 사용자에게 자료가 없다는 것을 알려야 한다
        <p>자료가 없습니다</p>
      )}
    </>
  )
}

function Recharts({Dustdata}) {

  console.log(Dustdata);

  const chartData = Dustdata.map(item => {
    return {
      count: Dustdata.length,
      통합대기환경수치: item["khaiValue"],
      time: item["dataTime"],
      pmValue: item["pm10Value"]
    }
  })

  return (
    <div style={{height: "300px"}}>
      <ResponsiveContainer width="100%" height="100%">
        <BarChart
          width={500}
          height={300}
          data={chartData}
          margin={{
            top: 5,
            right: 30,
            left: 20,
            bottom: 5,
          }}
        >
          <CartesianGrid strokeDasharray="3 3" />
          <XAxis dataKey="time" />
          <YAxis type="number" domain={[0, 250]}/>
          <Tooltip />
          <Legend />
          <Bar dataKey="통합대기환경수치" fill="#8884d8" />
          <Bar dataKey="pmValue" fill="#82ca9d" />
        </BarChart>
      </ResponsiveContainer>
    </div>
  );
}




